import React, { Component } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { NavLink, Redirect } from 'react-router-dom';
import WorkLink from './WorkLink.js';
import { firestoreConnect } from 'react-redux-firebase';


class HomeScreen extends Component {
    constructor(){
        super();
        this.initial = 1
        this.state={
        tempList:[],
        initial: 1,
        
    }}
    
    test=()=>{
        return this.props.wireframer
    }
    render() {
        if (!this.props.auth.uid) {
            return <Redirect to="/login" />;
        }
        return (
            
            <div className="dashboard container">
                
                <div className="row">
                    <div className="col s12 m4">
                        <div className="block">
                            <WorkLink />
                        </div>
                        
                    </div>

                    <div className="col s8">
                        <div className="banner">
                            Your works
                        </div>
                        
                        {/* <div className="home_new_list_container">
                                <button className="btn-large waves-effect waves-light" onClick={this.handleNewList}>
                                    Create a New To Do List
                                </button>
                        </div> */}
                    </div>
                
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    const {email} = state.firebase.auth
    const wireframer = state.firestore.data.wireframer
    
    return {
        auth: state.firebase.auth,
        email,
        wireframer: state.firestore.ordered.wireframer
        
    };
};

export default compose(
    connect(mapStateToProps),
    firestoreConnect([
      { collection: 'wireframer' },
    ]),
)(HomeScreen);