import React, { Component } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { firestoreConnect } from 'react-redux-firebase';
import { Redirect } from 'react-router-dom';




export class WorkLink extends Component {
    constructor(){
        super()
        window.call = this
        this.state={
            delete:""
        }
    }
    test=()=>{
        let email = this.props.auth.email
        let wireframerLists = this.props.data.wireframer[email].wireframerLists;
        console.log(wireframerLists)
    }
    redirect=(name,id)=>{
        let email = this.props.auth.email
        let wireframerLists = this.props.data.wireframer[email].wireframerLists;
        this.updateOrder(wireframerLists,id,name)
        
        //window.location.href = "http://localhost:3000/"+name+"/"+id+"/edit"
    }
    handleNewWorks=()=>{
        let index = this.props.list.length
        let ref =this.props.firestore.collection("wireframer").doc(this.props.email)
        let newWork={
            Control:[],
            height:"300",
            isRecent:false,
            key:this.props.list.length,
            name:"temp",
            width:"300"
        }
        //this.setState({tempList:[...this.state.tempList,newWork]})
        this.props.list[this.props.list.length] = newWork
        ref.update({
            "wireframerLists":this.props.list
        })
        this.updateOrder(this.props.list,index,"temp")

    }
    updateOrder=(list,id,name)=>{
        let done = false
        let paixu = list
        let index = id
        console.log(paixu,id)
        for(let i=0;i<paixu.length;i++){
            paixu[i].isRecent = false
            
        }
        paixu[index].isRecent = true
        let tempName = paixu[index].name
        let newIndex = ""
        paixu.sort((a,b)=>(a.isRecent >b.isRecent) ? -1:1)
        for(let i=0;i<paixu.length;i++){
            paixu[i].key = i
            if(paixu[i].name == tempName){
                newIndex = i
            }
        }
        console.log(paixu)
        let ref = this.props.firestore.collection("wireframer").doc(this.props.auth.email)
        ref.update({
            "wireframerLists": paixu
        })
        console.log(newIndex)
        setTimeout(() => window.location.href = "http://localhost:3000/"+this.props.auth.email+"/"+newIndex+"/edit",500)
    }
    delete=(e)=>{
        // console.log(e)
        // e.stopPropagation();
        console.log(this.state.delete)
        let id = this.state.delete
        let email = this.props.auth.email
        let wireframerLists = this.props.data.wireframer[email].wireframerLists;
        wireframerLists.splice(id,1)
        for(let i=0;i<wireframerLists.length;i++){
            wireframerLists[i].key = i
        }
        let ref = this.props.firestore.collection("wireframer").doc(this.props.auth.email)
        ref.update({
            "wireframerLists": wireframerLists
        })
        console.log(wireframerLists)
        this.dialogClose();

    }
    dialogShow=(e,id)=>{
        id.stopPropagation();
        document.getElementById("mubu").style.display = "inline";
        this.state.delete = e;
    }
    dialogClose=(e)=>{
        document.getElementById("mubu").style.display = "";
        this.state.delete = "";
    }
    display=(e)=>{
       e.stopPropagation();
       console.log("d");
    }
    displaytop=()=>{
        console.log(Object.keys(this.props.data.wireframer));
    }
    render() {
        if (!this.props.auth.uid) {
            return <Redirect to="/login" />;
        }
        const {email} = this.props.auth;
        const wireframer = this.props.data.wireframer; 
        
        if(!this.props.arr.includes(email)){
            return(
                <div className ="collection">
                <span className= "collection-item" onClick ={this.displaytop}>
                        default
                    <span className = "badge material-icons" onClick={this.display}>
                        delete
                    </span>
                </span>
                
            </div>
            )
            
        }
        const user = this.props.data.wireframer[email];
        const {wireframerLists} = this.props.data.wireframer[email];
        return (
            <div>
            <div className ="collection">
                {wireframerLists && wireframerLists.map(wireframerList=> (
                    <a href="#" className ="collection-item" onClick={this.redirect.bind(this,wireframerList.name,wireframerList.key)}>
                        {wireframerList.name}
                        <span className ="badge material-icons" onClick ={this.dialogShow.bind(this,wireframerList.key)}>
                            delete
                        </span>
                    </a>
                ))}
                
            </div>
            <button className="btn-small waves-effect waves-light" onClick={this.handleNewWorks}>Create a New To Do List</button>
            <div id="mubu">
                <dialog open style={{zIndex:99,left:'-3%',top:'28%'}}>
                    
                    <p>Do you want to delete selected wireframer</p>
                    <button onClick={this.delete}>Yes</button>
                    <button onClick={this.dialogClose}>No</button>
                </dialog>
            </div>

            </div>
            
        )
    }
}
const mapStateToProps = (state) => {
    const {wireframer} = state.firestore.data;
    const arry = wireframer ? Object.keys(wireframer):[];
    const {email} = state.firebase.auth
    const list = wireframer ? wireframer[email].wireframerLists:[]
    return {
        data:state.firestore.data,
        auth: state.firebase.auth,
        arr: arry,
        list,
        email
    };
};


export default compose(
    connect(mapStateToProps),
    firestoreConnect([
      { collection: 'wireframer' },
    ]),
)(WorkLink);
