import React from 'react'
import { connect } from 'react-redux';
import todoJson from './TestTodoListData.json'
import { getFirestore } from 'redux-firestore';
import testData from './TestData.json';
import { NavLink, Redirect } from 'react-router-dom';

class DatabaseTester extends React.Component {

    // NOTE, BY KEEPING THE DATABASE PUBLIC YOU CAN
    // DO THIS ANY TIME YOU LIKE WITHOUT HAVING
    // TO LOG IN
    handleClear = () => {
        const fireStore = getFirestore();
        fireStore.collection('todoLists').get().then(function(querySnapshot){
            querySnapshot.forEach(function(doc) {
                console.log("deleting " + doc.id);
                fireStore.collection('todoLists').doc(doc.id).delete();
            })
        });
    }
    handleClearUp =() =>{
        const fireStore = getFirestore();
        fireStore.collection('wireframer').get().then(function(querySnapshot){
            querySnapshot.forEach(function(doc){
                fireStore.collection('wireframer').doc("administrator@wireframer.com").delete();
            })
        });
    }

    handleReset = () => {
        const fireStore = getFirestore();
        todoJson.todoLists.forEach(todoListJson => {
            fireStore.collection('todoLists').add({
                    name: todoListJson.name,
                    owner: todoListJson.owner,
                    items: todoListJson.items
                }).then(() => {
                    console.log("DATABASE RESET");
                }).catch((err) => {
                    console.log(err);
                });
        });
    }
    handleSet = () =>{
        const fireStore = getFirestore();
        testData.wireframer.forEach(dataJson => {
            fireStore.collection('wireframer').doc(dataJson.userID).set({
                userID:dataJson.userID,
                wireframerLists: dataJson.wireframerLists,
                key:dataJson.key
            }).then(()=>{
                console.log("DATABASE RESET OK");
            }).catch((err)=>{console.log(err);});
        });
        
    }

    render() {
        if(!this.props.auth.uid){
            return <Redirect to="/login" />;
            
        }
        if(this.props.auth.email != "administrator@wireframer.com"){
            return <Redirect to="/" />;
        }
        return (
            
            <div>
                <button onClick={this.handleClearUp}>Clear Database</button>
                <button onClick={this.handleSet}>Reset WireFrame</button>
            </div>)
    }
}

const mapStateToProps = function (state) {
    return {
        auth: state.firebase.auth,
        firebase: state.firebase
    };
}

export default connect(mapStateToProps)(DatabaseTester);