import React, { Component } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { firestoreConnect } from 'react-redux-firebase';
import { Rnd } from "react-rnd";
import { Redirect } from 'react-router-dom';

export class Editting extends Component {
    constructor(){
        super()
        window.call = this
        this.initial = 1;
        this.state = {
            wireHeight: "",
            wireWidth: "",
            height:"",
            width:"",
            zoom : 1,
            templist:[],
            tempControls:[],
            forceRender:null,
            selected:{id:"initial State"},
            Properties:"",
            FontSize:"",
            BgC:"",
            BdC:"",
            BdT:"",
            BdR:"",
            id:"",
            fontColor:"",
            name:"",
            saved:false,
            index:"pointer",
            
        }
    }
    test=(e)=>{
        console.log(this.tempControls)
    }
    mouseSelect=(e)=>{
    
        let baimingdan = ["Properties","FontSize","BgC","BdC","BdT","BdR","property_area","fontColor"]
        this.setState({selected:e.target});
        console.log(this.state.selected.id);
        let regex = /control\d+/g;
        if(this.state.selected.id.match(regex)){
            let digitIndex= this.state.selected.id.substring(7,(this.state.selected.id.length));
            
            let object = this.state.tempControls[digitIndex]
            this.setState({Properties:object['Properties']})
            this.setState({FontSize:object['Font Size']})
            this.setState({BgC:object['Backgrond:']})
            this.setState({BdC:object['Border Color']})
            this.setState({BdT:object['Border Thickness']})
            this.setState({BdR:object['Border Radius']})
            this.setState({fontColor:object['fontColor']})
            this.setState({id:digitIndex})
            document.getElementById("topR"+digitIndex).style.display =""
            document.getElementById("topL"+digitIndex).style.display =""
            document.getElementById("botR"+digitIndex).style.display =""
            document.getElementById("botL"+digitIndex).style.display =""
            if(Number.isInteger(this.state.index-0) && this.state.index!=digitIndex){
                document.getElementById("topR"+this.state.index).style.display ="none"
                document.getElementById("topL"+this.state.index).style.display ="none"
                document.getElementById("botR"+this.state.index).style.display ="none"
                document.getElementById("botL"+this.state.index).style.display ="none"
            }
            this.state.index = digitIndex
        }else if(baimingdan.includes(this.state.selected.id)){

        }
        else{
            this.setState({Properties:""})
            this.setState({FontSize:""})
            this.setState({BgC:""})
            this.setState({BdC:""})
            this.setState({BdT:""})
            this.setState({BdR:""})
            this.setState({id:""})
            this.setState({fontColor:""})
            if(Number.isInteger(this.state.index-0)){
                document.getElementById("topR"+this.state.index).style.display ="none"
                document.getElementById("topL"+this.state.index).style.display ="none"
                document.getElementById("botR"+this.state.index).style.display ="none"
                document.getElementById("botL"+this.state.index).style.display ="none"
            }
        }
    }

    createElement=(control)=>{
        let id = control.id;
        
        if(control.type =="button"){
            return <Rnd 
                        size={{ width: control.width,  height: control.height }}
                        position={{ x: control.x, y: control.y }}
                        onDragStop={(e, d) =>  {this.setState({tempControls:this.state.tempControls[id].x=d.x,tempControls:this.state.tempControls[id].y=d.y})}}
                        onResizeStop={(e, direction, ref, delta, position) => {
                      this.setState({
                        tempControls: this.state.tempControls[id].width = ref.style.width,
                        tempControls: this.state.tempControls[id].height = ref.style.height,
                        tempControls: this.state.tempControls[id].x = position.x,
                        tempControls: this.state.tempControls[id].y = position.y,
                        ...position,
                      });
                        
                    }} bounds={'parent'}>
                    <button id={"control"+control.id} 
                            className="C_button" 
                            style={{position:"absolute",display:"inline-block",width:'inherit',height:'inherit'
                                ,fontSize:control['Font Size']+'px',color:control["fontColor"]+'',
                                backgroundColor:control['Backgrond:']+'',borderColor:control['Border Color']+'',
                                borderWidth:control['Border Thickness']+'px',borderRadius:control['Border Radius']+'%'
                        }}
                            >{control['Properties']}
                    </button>
                    <div  id={"topR"+control.id} style={{position:'absolute',width:20, height: 20, right: -10, top: -10, display:'none'}}>&#9644;</div>
                    <div  id={"topL"+control.id} style={{position:'absolute',width:20, height: 20, left: -10, top: -10,display:'none'}}>&#9644;</div>
                    <div  id={"botR"+control.id} style={{position:'absolute',width:20, height: 20, right: -10, bottom: -10,display:'none'}}>&#9644;</div>
                    <div  id={"botL"+control.id} style={{position:'absolute',width:20, height: 20, left: -10, bottom: -10,display:'none'}}>&#9644;</div>

                </Rnd>
        }else if(control.type=="label"){
            return <Rnd 
                        size={{ width: control.width,  height: control.height }}
                        position={{ x: control.x, y: control.y }}
                        onDragStop={(e, d) =>  {this.setState({tempControls:this.state.tempControls[id].x=d.x,tempControls:this.state.tempControls[id].y=d.y})}}
                        onResizeStop={(e, direction, ref, delta, position) => {
                            
                            this.setState({
                              tempControls: this.state.tempControls[id].width = ref.style.width,
                              tempControls: this.state.tempControls[id].height = ref.style.height,
                              tempControls: this.state.tempControls[id].x = position.x,
                              tempControls: this.state.tempControls[id].y = position.y,
                              ...position,
                            });
                          }}
                          bounds={'parent'}
                        
                    >           
                        <label id={"control"+control.id} 
                            className="C_label" 
                            style={{position:"absolute",display:"inline-block",width:'inherit',height:'inherit'
                                ,fontSize:control['Font Size']+'px',color:control["fontColor"]+'',
                                backgroundColor:control['Backgrond:']+'',borderColor:control['Border Color']+'',
                                borderWidth:control['Border Thickness']+'px',borderRadius:control['Border Radius']+'%'

                            }}>{control['Properties']}
                        </label>
                        <div  id={"topR"+control.id} style={{position:'absolute',width:20, height: 20, right: -10, top: -10, display:'none'}}>&#9644;</div>
                    <div  id={"topL"+control.id} style={{position:'absolute',width:20, height: 20, left: -10, top: -10,display:'none'}}>&#9644;</div>
                    <div  id={"botR"+control.id} style={{position:'absolute',width:20, height: 20, right: -10, bottom: -10,display:'none'}}>&#9644;</div>
                    <div  id={"botL"+control.id} style={{position:'absolute',width:20, height: 20, left: -10, bottom: -10,display:'none'}}>&#9644;</div>
                    </Rnd>
        }else if(control.type=="textfield"){
            return <Rnd 
                        size={{ width: control.width,  height: control.height }}
                        position={{ x: control.x, y: control.y }}
                        onDragStop={(e, d) =>  {this.setState({tempControls:this.state.tempControls[id].x=d.x,tempControls:this.state.tempControls[id].y=d.y})}}
                        onResizeStop={(e, direction, ref, delta, position) => {
                            this.setState({
                              tempControls: this.state.tempControls[id].width = ref.style.width,
                              tempControls: this.state.tempControls[id].height = ref.style.height,
                              tempControls: this.state.tempControls[id].x = position.x,
                              tempControls: this.state.tempControls[id].y = position.y,
                              ...position,
                            });
                          }}
                          bounds={'parent'}
                    >
                    <input id={"control"+control.id} 
                        className="browser-default C_input" 
                        type="text" placeholder={control['Properties']} 
                        style={{position:"absolute",display:"inline-block",width:'inherit',height:'inherit',
                                fontSize:control['Font Size']+'px',color:control["fontColor"]+'',
                                backgroundColor:control['Backgrond:']+'',borderColor:control['Border Color']+'',
                                borderWidth:control['Border Thickness']+'px',borderRadius:control['Border Radius']+'%'}}>
                    </input>
                    <div  id={"topR"+control.id} style={{position:'absolute',width:20, height: 20, right: -10, top: -10, display:'none'}}>&#9644;</div>
                    <div  id={"topL"+control.id} style={{position:'absolute',width:20, height: 20, left: -10, top: -10,display:'none'}}>&#9644;</div>
                    <div  id={"botR"+control.id} style={{position:'absolute',width:20, height: 20, right: -10, bottom: -10,display:'none'}}>&#9644;</div>
                    <div  id={"botL"+control.id} style={{position:'absolute',width:20, height: 20, left: -10, bottom: -10,display:'none'}}>&#9644;</div>
                    </Rnd>
        }else{
            return <Rnd 
                        size={{ width: control.width,  height: control.height }}
                        position={{ x: control.x, y: control.y }}
                        onDragStop={(e, d) =>  {this.setState({tempControls:this.state.tempControls[id].x=d.x,tempControls:this.state.tempControls[id].y=d.y})}}
                        onResizeStop={(e, direction, ref, delta, position) => {
                            this.setState({
                              tempControls: this.state.tempControls[id].width = ref.style.width,
                              tempControls: this.state.tempControls[id].height = ref.style.height,
                              tempControls: this.state.tempControls[id].x = position.x,
                              tempControls: this.state.tempControls[id].y = position.y,
                              ...position,
                            });
                          }}
                          bounds={'parent'}
                    >
                    <div id={"control"+control.id}   
                        className="C_div" 
                        style={{position:"absolute",display:"inline-block", width:'inherit',height:'inherit'
                                ,border:'solid',borderRadius:control['Border Radius']+'%',
                                borderColor:control['Border Color']+'',fontSize:control['Font Size']+'px',
                                backgroundColor:control['Backgrond:']+'',
                                borderWidth:control['Border Thickness']+'px',color:control["fontColor"]+''
                                }}>
                        {control['Properties']}
                    </div>
                    <div  id={"topR"+control.id} style={{position:'absolute',width:20, height: 20, right: -10, top: -10, display:'none'}}>&#9644;</div>
                    <div  id={"topL"+control.id} style={{position:'absolute',width:20, height: 20, left: -10, top: -10,display:'none'}}>&#9644;</div>
                    <div  id={"botR"+control.id} style={{position:'absolute',width:20, height: 20, right: -10, bottom: -10,display:'none'}}>&#9644;</div>
                    <div  id={"botL"+control.id} style={{position:'absolute',width:20, height: 20, left: -10, bottom: -10,display:'none'}}>	&#9644;</div>
                </Rnd>
        }
    }
    deleteControls=(e)=>{
        if(e.key == "Backspace"){
            let regex = /control\d+/g;
            if(this.state.selected.id.match(regex)){
                let digitIndex= this.state.selected.id.substring(7,(this.state.selected.id.length));
                console.log(digitIndex)
                document.getElementById("topR"+this.state.index).style.display ="none"
                document.getElementById("topL"+this.state.index).style.display ="none"
                document.getElementById("botR"+this.state.index).style.display ="none"
                document.getElementById("botL"+this.state.index).style.display ="none"
                this.setState({selected:{id:"-1"}})
                this.setState({tempControls:this.state.tempControls.splice(digitIndex,1)})
                this.setState({Properties:""})
                this.setState({FontSize:""})
                this.setState({BgC:""})
                this.setState({BdC:""})
                this.setState({BdT:""})
                this.setState({BdR:""})
                this.setState({id:""})
                this.state.index="pointer"
                this.state.saved = false
                for(let i=0;i<this.state.tempControls.length;i++){
                    this.state.tempControls[i].id=i
                    this.state.tempControls[i].key=i
                }

            }
        }
        else{
            //e.preventDefault();
            var ctrlKey = e.ctrlKey
            if(ctrlKey && e.keyCode==68){
                e.preventDefault();
                let regex = /control\d+/g;
                if(this.state.selected.id.match(regex)){
                let digitIndex= this.state.selected.id.substring(7,(this.state.selected.id.length));
                    let newControl = JSON.parse(JSON.stringify(this.state.tempControls[digitIndex]))
                    console.log(this.state.tempControls[digitIndex])
                    newControl.id = this.state.tempControls.length
                    newControl.key = this.state.tempControls.length
                    newControl.x +=100
                    
                    console.log(newControl)
                    this.state.tempControls[this.state.tempControls.length] = newControl;
                    this.setState({forceRender:1});
                    console.log(e.target.id);
                    this.state.saved = false
                }
            }else{
                return;
            }
        }
    }
    addControls=(e)=>{
        this.state.saved = false
        let width=0
        let height=0
        if(e.target.id =="button"){
            width=60
            height=30
        }else if(e.target.id =="label"){
            width=50
            height=29
        }else if(e.target.id =="container"){
            width=320
            height=200
        }else if(e.target.id =="textfield"){
            width=120
            height=25
        }
        let control = {
            "key": this.state.tempControls.length,
            "id":this.state.tempControls.length,
            "Properties": e.target.id,
            "Font Size": "14",
            "Backgrond:": "#d6d6d6",
            "Border Color": "#00000",
            "Border Thickness": 2,
            "Border Radius": 2 ,
            "type": e.target.id,
            "x":0,
            "y":0,
            "width":width,
            "height":height
        }
        this.state.tempControls[this.state.tempControls.length] = control;
        this.setState({forceRender:1});
        console.log(e.target.id);
    }
    
    zoom_in=(e)=>{
        this.state.zoom = this.state.zoom*2;
        console.log(this.state.zoom);
        document.getElementById("Wireframe").style.zoom = this.state.zoom;
        
    }
    zoom_out=(e)=>{
        this.state.zoom = this.state.zoom/2.0
        console.log(this.state.zoom)
        document.getElementById("Wireframe").style.zoom = this.state.zoom
        
        
    }

    handleHChange=(e)=>{
        this.state.saved = false
        let baimingdan = ["Properties","FontSize","BgC","BdC","BdT","BdR","fontColor"]
        document.getElementById("updateButton").disabled = false
        const { target } = e;
        if(baimingdan.includes(target.id)){
            if(this.state.id !=""){
                this.setState(state => ({
                    ...state,
                    [target.id]: target.value,
                }));
                if(target.id=="Properties"){
                    this.state.tempControls[this.state.id]['Properties'] = target.value
                    this.setState({forceRender:1})
                }else if(target.id=="FontSize"){
                    this.state.tempControls[this.state.id]['Font Size'] = target.value
                    this.setState({forceRender:1})
                }else if(target.id=="BgC"){
                    this.state.tempControls[this.state.id]['Backgrond:'] = target.value
                    this.setState({forceRender:1})
                }else if(target.id=="BdC"){
                    this.state.tempControls[this.state.id]['Border Color'] = target.value
                    this.setState({forceRender:1})
                }else if(target.id=="BdT"){
                    this.state.tempControls[this.state.id]['Border Thickness'] = target.value
                    this.setState({forceRender:1})
                }
                else if(target.id=="BdR"){
                    this.state.tempControls[this.state.id]['Border Radius'] = target.value
                    this.setState({forceRender:1})
                }else if(target.id=="fontColor"){
                    this.state.tempControls[this.state.id]['fontColor'] = target.value
                    this.setState({forceRender:1})
                }
            }
            return;
        }
        if(e.target.value >=1 && e.target.value <=5000 && e.target.value%1==0){
            this.setState(state => ({
                ...state,
                [target.id]: target.value,
            }));
        }else{
            this.setState(state => ({
                ...state,
                [target.id]: target.value,
            }));
            console.log(e.target.id)
            if(target.id=="wireHeight" || target.id=="wireWidth")
                window.alert("Please input an Integer between 1~5000")
        }
    }
    handldName=(e)=>{
        this.state.saved = false
        console.log(e.target.value)
        this.setState({name:e.target.value})
        this.state.templist[0].name=e.target.value
    }
    readyUpdate=()=>{
        let height = document.getElementById("wireHeight").value;
        let width = document.getElementById("wireWidth").value;
        
        this.state.height= height
        this.state.width = width
        let a = height>=1 && height<=5000 && height %1==0;
        let b = width>=1 && width<=5000 && width %1==0;
        if(a&&b){      
            document.getElementById("Wireframe").style.height = height+"px"
            document.getElementById("Wireframe").style.width = width+"px"  
            console.log(height);
            console.log(width);
            document.getElementById("updateButton").disabled = true;
        }
    }
    fireUpdate=()=>{
        this.state.saved= true;
        let height = document.getElementById("wireHeight").value;
        let width = document.getElementById("wireWidth").value;
        let paixu = this.state.templist;
        paixu[0].height = height;
        paixu[0].width = width;
        let ref =this.props.firestore.collection("wireframer").doc(this.props.email)
            ref.update({
                "wireframerLists": paixu
            });
    }
    previousPage=()=>{
        window.location.href = window.location.href = "http://localhost:3000/"
    }
    default=()=>{
        this.errro();
        var height = this.state.templist[0].height
        var width = this.state.templist[0].width
        var name = this.state.templist[0].name
        this.state.wireHeight = height
        this.state.wireWidth = width
        this.state.height = height
        this.state.width = width
        this.state.name = name
        document.getElementById("Wireframe").style.height = this.state.wireHeight+"px"
        document.getElementById("Wireframe").style.width = this.state.wireWidth+"px"
    }
    wantSave=(e)=>{
        if(this.state.saved){
            this.previousPage()
            return;
        }
            let sure = window.confirm("You have unsaved work. Do you want to leave now?")
            if(sure){
                this.previousPage()
            }
    }
    errro=()=>{
        if(this.props.list ==[]){
            return
        }
        var str = window.location.href;
        var res = str.split("/");
        var res = res[3]
        res = res.replace(/%/g," ")
        
        if(res != this.props.email)
            window.location.href="http://localhost:3000/";
    }
    render() {
        if (!this.props.auth.uid) {
            return <Redirect to="/login" />;
        }
        //this.errro()
        document.addEventListener("mousedown",this.mouseSelect)
        document.addEventListener("keydown",this.deleteControls)
        this.initial += 1
        // eslint-disable-next-line
        this.state.templist = this.props.list
        this.state.tempControls = this.props.controls

        if(this.initial==4){
            this.default()
        }
        
        const controls = this.state.tempControls;
        return (
            <div className="editPage">
                <div className="Button_area">
                    <div className="biankuang zoom_area">

                    <button className="btn-small btn-floating" onClick ={this.zoom_in}><i className="material-icons">zoom_in</i></button>
                    <button className="btn-small btn-floating" onClick ={this.zoom_out}><i className="material-icons">zoom_out</i></button>
                    <button className="btn-small btn-floating" onClick={this.fireUpdate}>Save</button>
                    <button className="btn-small btn-floating" onClick={this.wantSave}>Exit</button>
                    </div>
                    <div className="biankuang Controls_button">
                        <div className = "biankuang" id="container" onClick={this.addControls}>
                        </div>
                        <span id="Container_name">Container</span>
                        <br/>
                        <hr/>
                        <span id="label"href="#!" onClick={this.addControls}>Prompt for input</span>
                        <br/>
                        <span id="Label_name">Label</span>
                        <hr/>
                        <br/>
                        <button id="button"className="btn-small" onClick={this.addControls}>Submit</button>
                        <br/>
                        <span id="C_button_name">Button</span>
                        <hr/>
                        <br/>
                        <input id="textfield" className="browser-default" type="text" placeholder="Input" onClick={this.addControls}></input>
                        <span id="textinputname">Textfield</span>
                        <hr/> 
                        <span>height:  </span>
                        
                        <input className="browser-default" 
                            id="wireHeight" type="text" 
                            placeholder="height" 
                            onChange={this.handleHChange} 
                            value={this.state.wireHeight}
                            >
                        </input>
                        <br/>
                        <span>width:&#8194;&#8197;</span>
                        <input className="browser-default" 
                            id="wireWidth" type="text" 
                            placeholder="width" 
                            onChange={this.handleHChange} 
                            value={this.state.wireWidth}></input>
                        <br/>
                        <button id="updateButton" onClick={this.readyUpdate}>Update</button>
                        <br/>
                        <span>Name: </span>
                        <input className="browser-default" 
                            id = "name"
                            type="text" 
                            placeholder="name" 
                            onChange={this.handldName} 
                            value={this.state.name}
                            style={{width:'50%'}}
                            >
                        </input>                    
                    </div>   
                </div>
                <div className="display_area biankuang" >
                    <div id= "Wireframe"className="biankuang" >
                    {controls && controls.map(control=> (
                            this.createElement(control)
                    ))}
                    </div>    
                </div>
                

                <div className="properties_area biankuang" id="property_area">
                    Properties:
                    <br/>
                    <input id="Properties" className="Properties" type="text" placeholder="Properties" onChange={this.handleHChange} value={this.state.Properties}></input> 
                    Font Size: <input id="FontSize"className="Font_size" type="text" placeholder="Properties" onChange={this.handleHChange} value={this.state.FontSize}></input> 
                    <br/>
                    Font Color:  <input id="fontColor" type="color" className="fc" onChange={this.handleHChange} value={this.state.fontColor}></input>
                    <br/>
                    Background:  <input id="BgC" type="color" className="BgC" onChange={this.handleHChange} value={this.state.BgC}></input>
                    <br/>
                    Border Color: <input id="BdC" type="color" className="BC" onChange={this.handleHChange} value={this.state.BdC}></input>
                    <br/>
                    Border Thickness:  <input id="BdT" type="number" className="BT" onChange={this.handleHChange} value={this.state.BdT}></input>
                    <br/>
                    Border Radius:  <input id="BdR" type="number" className="BR" onChange={this.handleHChange} value={this.state.BdR}></input>
                </div>
            </div>
        )
        
    }
}

const mapStateToProps = (state, ownProps) => {
    const id = ownProps.match.params;
    const {email} = state.firebase.auth
    const wireframer = state.firestore.data.wireframer
    const info = wireframer ? wireframer[email].wireframerLists[id.id]:[]; // wireframerList's element
    const list = wireframer ? wireframer[email].wireframerLists:[]//WireframLists
    const collection = wireframer ? wireframer[email]:[]
    const controls = info? info.Control:[]
    return {
        auth: state.firebase.auth,
        id,
        info,
        email,
        controls,
        list,
        info,
        collection
    };
};
export default compose(
    connect(mapStateToProps),
    firestoreConnect([
        { collection: 'wireframer' },
    ]),
)(Editting);
